import './App.css';
import Game from './game/game';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css'; // Importa los estilos de React Toastify


function App() {
  return (
    <div className="App">
      <Game />
      <ToastContainer
        position="top-center" // Mantiene la posicin en la parte superior central
        autoClose={3000} // Tiempo de cierre automtico en milisegundos
        hideProgressBar={false} // Muestra la barra de progreso
        newestOnTop={true} // Las notificaciones ms recientes aparecen primero
        closeOnClick // Cierra la notificacin al hacer clic
        pauseOnHover // Pausa el temporizador al pasar el mouse
        draggable // Permite arrastrar la notificacin
        theme='colored'
        style={{ top: '70px', right: '100px' }} // Ajusta la posicin vertical (ms abajo)
      />
    </div>
  );
}

export default App;
